.Navbar {
    position: fixed;
    top: 352px;
    right: 27px;
    z-index: 10;
    display: flex;
    flex-direction: column;
   
    & .item {
      width: 13px;
      height: 13px;
      border-radius: 50%;
      outline: none;
      border: none;
      padding: 0;
      margin-bottom: 7px;
      transition: 1s;
      background: #ffffff;

      &.active {
        background: #f78b1f;
      }
    }
  }
  